@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@500;600;700&family=Lato:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap);


.wrapper{height:100vh;background-color:#9439ef;padding:2%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.title{text-align:center;color:#fff}.title svg{vertical-align:middle;fill:#ff1b6b}.form{display:flex;flex-direction:column;grid-gap:2rem;gap:2rem;width:95%;max-width:600px}.label{color:#fff}.form__input{border:none;height:3rem;background:rgba(255,255,255,.85);box-shadow:0 8px 32px 0 rgba(31,38,135,.37);backdrop-filter:blur(4px);-webkit-backdrop-filter:blur(4px);border:1px solid rgba(177,112,112,.18);font-size:1.2rem;padding:5px}.form__input:focus{border:2px solid #45caff;outline:none}.btn{padding:14px;border:none;background:linear-gradient(90deg, #5fcdf2 25%, #9439ef 77%);color:#000;box-shadow:0px 10px 21px 0px rgba(255,255,255,.75)}.btn:hover{background:linear-gradient(90deg, rgba(95, 205, 242, 0.2) 25%, #9439ef 77%)}.btn:active{border:2px solid #000;box-shadow:0px 10px 21px 0px rgba(255,255,255,.3)}.signup{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem}.signUpForm{display:flex;grid-gap:2rem;gap:2rem;flex-direction:column}@media screen and (min-width: 650px){.signUpForm{flex-direction:row}}.button__input{padding:10px;background-color:#fff;border:none}.button__input svg{vertical-align:text-bottom}.button__input:hover{background-color:#ff1b6b}.button__input:active{border:solid 1px #ff1b6b;box-shadow:0px 10px 21px 0px rgba(255,255,255,.3)}
.header{display:flex;padding:10px;height:50spx;background-color:#9439ef;color:#fff;display:flex;justify-content:space-between}.header .logo svg{fill:#ff1b6b}.header .btn{padding:.3rem .7rem}
*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}h1,h2,h3,h4,h5,h6{margin:0}html{font-family:"Lato",sans-serif;scroll-behavior:smooth}a{text-decoration:none}a,button{cursor:pointer}ul,li{list-style-type:none}img{max-width:100%;display:block}input,button,textarea,select{font:inherit}
